"use client";

import { useMemo, useState } from "react";

const PIN = "8989";
const DEFAULT_GRID_WIDTH = 32;
const DEFAULT_GRID_HEIGHT = 20;
const MIN_SIZE = 8;
const MAX_WIDTH = 64;
const MAX_HEIGHT = 40;

const palette = [
  { id: 0, label: "Piso tile1", image: "/tilesets/tile1.png" },
  { id: 1, label: "Piso tile2", image: "/tilesets/tile2.png" }
] as const;

type GridMatrix = number[][];

function buildEmptyGrid(width: number, height: number): GridMatrix {
  return Array.from({ length: height }, () => Array(width).fill(0));
}

export default function EditorPage() {
  const [pin, setPin] = useState("");
  const [unlocked, setUnlocked] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [selectedTile, setSelectedTile] = useState<number>(palette[0].id);
  const [gridWidth, setGridWidth] = useState(DEFAULT_GRID_WIDTH);
  const [gridHeight, setGridHeight] = useState(DEFAULT_GRID_HEIGHT);
  const [grid, setGrid] = useState<GridMatrix>(() =>
    buildEmptyGrid(DEFAULT_GRID_WIDTH, DEFAULT_GRID_HEIGHT)
  );
  const [copied, setCopied] = useState(false);

  const output = useMemo(() => JSON.stringify(grid), [grid]);

  function authenticate(event: React.FormEvent<HTMLFormElement>) {
    event.preventDefault();
    if (pin === PIN) {
      setUnlocked(true);
      setError(null);
    } else {
      setError("PIN inválido");
    }
  }

  function paintCell(rowIndex: number, columnIndex: number) {
    setGrid((previous) =>
      previous.map((row, r) =>
        row.map((value, c) => {
          if (r === rowIndex && c === columnIndex) {
            return selectedTile;
          }
          return value;
        })
      )
    );
  }

  function clearGrid() {
    setGrid(buildEmptyGrid(gridWidth, gridHeight));
  }

  function handleDimensionChange(width: number, height: number) {
    const clampedWidth = clamp(width, MIN_SIZE, MAX_WIDTH);
    const clampedHeight = clamp(height, MIN_SIZE, MAX_HEIGHT);
    setGrid((previous) =>
      Array.from({ length: clampedHeight }, (_, rowIndex) =>
        Array.from({ length: clampedWidth }, (_, columnIndex) => previous[rowIndex]?.[columnIndex] ?? 0)
      )
    );
    setGridWidth(clampedWidth);
    setGridHeight(clampedHeight);
  }

  async function copyToClipboard() {
    await navigator.clipboard.writeText(output);
    setCopied(true);
    setTimeout(() => setCopied(false), 1500);
  }

  function saveToFile() {
    const blob = new Blob([output], { type: "application/json" });
    const url = URL.createObjectURL(blob);
    const anchor = document.createElement("a");
    anchor.href = url;
    anchor.download = "map_city.json";
    anchor.click();
    URL.revokeObjectURL(url);
  }

  return (
    <section>
      <div className="card">
        <h2>Admin / Editor de Mapas</h2>
        <p>Restrito. Use o PIN <strong>8989</strong> para habilitar o editor de tiles da cidade.</p>
        {!unlocked && (
          <form onSubmit={authenticate}>
            <label htmlFor="pin">PIN</label>
            <input
              id="pin"
              type="password"
              value={pin}
              onChange={(event) => setPin(event.target.value)}
              placeholder="****"
            />
            {error && <p>{error}</p>}
            <button className="button">Entrar</button>
          </form>
        )}
        {unlocked && (
          <p>
            Acesso concedido. Configure abaixo (grid padrão {DEFAULT_GRID_WIDTH}×{DEFAULT_GRID_HEIGHT}) e
            exporte para o Phaser.
          </p>
        )}
      </div>

      {unlocked && (
        <>
          <div className="card">
            <h3>Paleta de Tiles</h3>
            <p>Ambos possuem 100×100 px e são reamostrados para blocos de 32 px no mapa.</p>
            <div style={{ display: "flex", gap: 12, flexWrap: "wrap" }}>
              {palette.map((tile) => (
                <button
                  key={tile.id}
                  type="button"
                  onClick={() => setSelectedTile(tile.id)}
                  style={{
                    borderRadius: 12,
                    padding: 0,
                    width: 64,
                    height: 64,
                    border: selectedTile === tile.id ? "2px solid #48c6ef" : "1px solid rgba(255,255,255,0.2)",
                    backgroundImage: `url(${tile.image})`,
                    backgroundSize: "cover",
                    backgroundPosition: "center",
                    cursor: "pointer"
                  }}
                  title={tile.label}
                />
              ))}
            </div>
          </div>

          <div className="card">
            <h3>Dimensões do Grid</h3>
            <p>Aumente ou diminua largura/altura conforme necessidade. Limites: largura até 64, altura até 40.</p>
            <div className="grid" style={{ gridTemplateColumns: "repeat(auto-fit, minmax(220px, 1fr))" }}>
              <div>
                <label htmlFor="grid-width">Largura (colunas)</label>
                <input
                  id="grid-width"
                  type="number"
                  min={MIN_SIZE}
                  max={MAX_WIDTH}
                  value={gridWidth}
                  onChange={(event) => handleDimensionChange(Number(event.target.value), gridHeight)}
                />
              </div>
              <div>
                <label htmlFor="grid-height">Altura (linhas)</label>
                <input
                  id="grid-height"
                  type="number"
                  min={MIN_SIZE}
                  max={MAX_HEIGHT}
                  value={gridHeight}
                  onChange={(event) => handleDimensionChange(gridWidth, Number(event.target.value))}
                />
              </div>
              <div style={{ alignSelf: "end", display: "flex", gap: 8 }}>
                <button
                  className="button"
                  type="button"
                  onClick={() => handleDimensionChange(gridWidth + 2, gridHeight)}
                >
                  + largura
                </button>
                <button
                  className="button"
                  type="button"
                  onClick={() => handleDimensionChange(gridWidth, gridHeight + 2)}
                >
                  + altura
                </button>
              </div>
            </div>
          </div>

          <div className="card">
            <h3>Grid ({gridWidth} × {gridHeight})</h3>
            <p>Cada célula representa 32 px no Phaser. Clique para pintar.</p>
            <div
              style={{
                display: "grid",
                gridTemplateColumns: `repeat(${gridWidth}, 24px)`,
                gap: 2,
                justifyContent: "flex-start",
                overflowX: "auto"
              }}
            >
              {grid.map((row, rowIndex) =>
                row.map((value, columnIndex) => (
                  <button
                    type="button"
                    key={`${rowIndex}-${columnIndex}`}
                    onClick={() => paintCell(rowIndex, columnIndex)}
                    style={{
                      width: 24,
                      height: 24,
                      padding: 0,
                      border: "1px solid rgba(255,255,255,0.2)",
                      borderRadius: 4,
                      backgroundImage: `url(${
                        palette.find((tile) => tile.id === value)?.image ?? palette[0].image
                      })`,
                      backgroundSize: "cover",
                      backgroundPosition: "center"
                    }}
                    aria-label={`linha ${rowIndex + 1}, coluna ${columnIndex + 1}`}
                  />
                ))
              )}
            </div>
            <div style={{ marginTop: 16, display: "flex", flexWrap: "wrap", gap: 12 }}>
              <button className="button" type="button" onClick={clearGrid}>
                Limpar grid
              </button>
              <button className="button" type="button" onClick={copyToClipboard}>
                {copied ? "Copiado!" : "Copiar JSON"}
              </button>
              <button className="button" type="button" onClick={saveToFile}>
                Salvar mapa (.json)
              </button>
            </div>
            <label htmlFor="grid-json" style={{ marginTop: 16 }}>
              JSON Exportado
            </label>
            <textarea
              id="grid-json"
              readOnly
              value={output}
              style={{ width: "100%", minHeight: 120 }}
            />
          </div>
        </>
      )}
    </section>
  );
}

function clamp(value: number, min: number, max: number) {
  return Math.max(min, Math.min(max, Number.isNaN(value) ? min : value));
}
